import React, { ReactNode } from 'react';
import AppContext from '../../context/context';
import './NavBar.scss';
import CommandButtonFC from './CommandButtonFC';

const NavBar: React.FC<{tip?: ReactNode}> = (props) => (
  <AppContext.Consumer>
      {({ data, submitCommand }) => (
        <div className={`NavBar selected${data.nav.mainArea+1}`}>
          <CommandButtonFC name='Relics' command='nav main 0' />
          <CommandButtonFC name='Reset' command='nav main 1' hidden={(datamap)=>{return datamap.prophetData.prestigeUnlocked === false && data.prophetData.holiness === 0}}/>
          <CommandButtonFC name='Followers' command='nav main 2' hidden={(datamap)=>{return datamap.prophetData.congregation.unlocked === false}} />
          <CommandButtonFC name='Adornments' command='nav main 3' hidden={(datamap)=>{return datamap.followers.adornmentsUnlocked === false}} />
          <CommandButtonFC name='Miracles' command='nav main 4' />
        </div>
      )}
    </AppContext.Consumer>
)

export default NavBar;