import CoreEngine from "./CoreEngine";
import ProphetModule from "./modules/Prophet";
import GameTime from "./modules/Gametime";
import NavArea from "./modules/NavArea";
import Followers from "./modules/Followers";
import { MINUTE_MS } from "./util";
import Rocket from "./modules/Rocket";
import NameAndCode from "./modules/NameAndCode";

export default class Engine extends CoreEngine {
    
    processDelta = (delta: number) => {
        this.rocket.processDelta(delta)
        if (this.datamap.nacData.name === 'DEVC') {
            delta = delta * 10;
        }
        //console.log('tick');
        this.prophet.processDetla(delta)
        
        this.gameTime.processDelta(delta)
            this.stopOffline();

    }

    

    newprocessDelta = (delta: number) => {
        delta = delta * 6;
        //console.log('tick');
        if (delta < MINUTE_MS ) { 
            this.prophet.processDetla(delta) 
            this.gameTime.processDelta(delta)
        }
        else {
            let ticks = Math.floor(delta/MINUTE_MS);

            
            for (let i = 0; i < ticks; i++) {
                this.prophet.processDetla(MINUTE_MS) 
                this.gameTime.processDelta(MINUTE_MS)
            }
            this.prophet.processDetla(delta%MINUTE_MS) 
            this.gameTime.processDelta(delta%MINUTE_MS)

            this.stopOffline();
        }
    }
    
    startOffline (delta: number) {
        this.datamap.offline = {happening: true,totaltime: delta}
        this.notify();
    }

    stopOffline () {
        this.datamap.offline.happening = false;
    }

    executeCommand = (command: string) => {
        let commands = command.split(' ');
        //console.log(`process ${command}, ${commands}`);

        switch (commands[0]) {
            case '1hr':
                this.processDelta(60 * 60 * 1000);
                break;

            case 'prophet':
                this.prophet.processCommands(commands)
                break;

            case 'gametime':
                this.gameTime.processCommands(commands);
                break;

            case 'save':
                this.save();
                break;

            case 'load':
                this.load();
                break;

            case 'nav':
                if (commands[1] === 'main') {
                    this.nav.changeMainArea(commands[2])
                } else if (commands[1] === 'sub') {
                    this.nav.changeSubArea(commands[2])
                }
                break;

            case 'followers':
                this.followers.handle_followers_command(commands)
                break;

            case 'rocket':
                this.rocket.handle_rocket_command(commands);
                break;
            case 'keypress':
                this.handleKey(commands[1]);
                break;

            case 'nac':
                this.nac.nacCommand(commands)
                break;
       
            default:
                console.log('...the dev made a mistake.')
                break;

        }

        this.notify();
    }

    handleKey = (key: string) => {
        switch (key) {
            case 'p':
                this.prophet.pray();
                break;

            case 'm':
                for (let index = 0; index < 10; index++) {                    
                    this.prophet.buyMaxRelic(index.toString())
                }
                break;

            case 'r':
                this.prophet.prestige();
                break;
        
            default:
                break;
        }
    }

    load = () => {
        this.quietLoad();
        this.startOffline(Date.now()-this.datamap.last)
        this.notify();
    }

    prophet = new ProphetModule(this);
    gameTime = new GameTime(this);
    nav = new NavArea(this);
    followers = new Followers(this)
    rocket = new Rocket(this);
    nac = new NameAndCode(this);
}

